import React from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Button from 'react-bootstrap/Button'
import Dropdown from 'react-bootstrap/Dropdown'

import { NavLink } from 'react-router-dom';

// import icons
import kel_morianIcon from '../styles/kel-morian.png'


class HomeNavbar extends React.Component {
    user = '';
    session = '';
    name = '';

    state = {
        loggerIn: false
    }

    logoutHandler = async () => {
        await fetch(`/api/logout?session=${this.session}&user=${this.user}`, {
            method: 'post', redirect: 'follow'
        });
        localStorage.removeItem('user');
        localStorage.removeItem('userName');
        localStorage.removeItem('session');
        sessionStorage.removeItem('user');
        sessionStorage.removeItem('userName');
        sessionStorage.removeItem('session');
    }

    componentWillMount() {
        if(localStorage.getItem('user')) {
            this.user = localStorage.getItem('user');
        } else {
            if(sessionStorage.getItem('user')) {
                this.user = sessionStorage.getItem('user');
            }
        }
        if(localStorage.getItem('session')) {
            this.session = localStorage.getItem('session');
        } else {
            if(sessionStorage.getItem('session')) {
                this.session = sessionStorage.getItem('session');
            }
        }
        if(localStorage.getItem('userName')) {
            this.name = localStorage.getItem('userName');
        } else {
            if(sessionStorage.getItem('userName')) {
                this.name = sessionStorage.getItem('userName');
            }
        }

        if(this.user !== ''){
            this.setState({
                loggedIn: true
            });
        }
    }

    render() {
        return (
            <Navbar variant="dark" bg="dark" expand="lg">
                <Navbar.Brand >
                    <NavLink to="/">
                        <img height="30" src={kel_morianIcon} alt="icon"></img>
                    </NavLink>
                </Navbar.Brand>

                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link>
                            <NavLink to="/userguide">
                                Manual de usuario
                            </NavLink>
                        </Nav.Link>
                        <Nav.Link>
                            <NavLink to="/lab/apps">
                                Laboratorio
                            </NavLink>
                        </Nav.Link>
                    </Nav>
                    {this.user === '' &&
                        <Nav inline>
                            <NavLink to="/signin">
                                <Button variant="secondary">Iniciar sesión</Button>
                            </NavLink>
                        </Nav>
                    }
                    {this.user !== '' &&
                        <Dropdown>
                            <Dropdown.Toggle variant="secondary" id="sessionDropDown">
                                {this.name}
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <Dropdown.Item onClick={this.logoutHandler}>Cerrar sesión</Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    }
                </Navbar.Collapse>
            </Navbar>
        )
    }
}

export default HomeNavbar



