import React from 'react';
import HomeNavbar from '../components/navbar/homeNavbar';
import Container from 'react-bootstrap/Container';
import Carousel from 'react-bootstrap/Carousel';


import cpuIcon from '../components/styles/img/cpu.svg';
import shieldIcon from '../components/styles/img/shield-lock.svg';
import serverIcon from '../components/styles/img/server.svg';
import wifiIcon from '../components/styles/img/wifi.svg';


const HomePage = () => (
    <div>
        <HomeNavbar />

        <Container className="mt-3">
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={serverIcon}
                        alt="First slide"
                        style={{height: '85vh'}}
                    />
                    <Carousel.Caption>
                        <h3>Sin servidores</h3>
                        <p>Quítate de encima el trabajo del backend. Enfócate en lo que haces mejor, tu aplicación</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={wifiIcon}
                        alt="Third slide"
                        style={{height: '85vh'}}
                    />

                    <Carousel.Caption>
                        <h3>Comunicación en tiempo real</h3>
                        <p>Transmite información entre aplicaciones y dispositivos en tiempo real</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={cpuIcon}
                        alt="Third slide"
                        style={{height: '85vh'}}
                    />

                    <Carousel.Caption>
                        <h3>Una sola interfaz</h3>
                        <p>Utiliza la misma API para el desarrollo de aplicaciones y dispositivos</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={shieldIcon}
                        alt="Third slide"
                        style={{height: '85vh'}}
                    />

                    <Carousel.Caption>
                        <h3>Protege tus aplicaciones</h3>
                        <p>Controla el nivel de acceso de tu aplicación entre pública y privada</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </Container>
    </div>
)


export default HomePage;


